.AddNewTeamForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10%;
  width: 100%;
  height: 30%;
}

.title {
  color: white;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 160%;
  font-weight: bold;
}

.TeamNameField {
  width: 60%;
  background-color: white;
  border-radius: 5px;
  font-family: "Roboto Condensed", sans-serif;
}

.AddButton {
  height: 20%;
  width: 40%;
  font-family: "Roboto Condensed", sans-serif;
}
